px;\n}\n\nbody h1,\nh2 {\n  color: var(--main-color);\n  font-weight: 400;\n}\n\n#content {\n  flex: 1;\n}\n\n/* Footer */\n\nfooter {\n  display: grid;\n  justify-items: center;\n  padding: 10px;\n}\n\nfooter p,\na {\n  color: rgb(171, 171, 171);\n  font-size: 10px;\n  text-decoration: none;\n}\n\n/* Header with navbar */\n\n.main-header {\n  padding: 20px 15px;\n  display: flex;\n  justify-content: center;\n  position: relative;\n}\n\n.restaurant-pages-nav .main-buttons-container button {\n  width: auto;\n  white-space: nowrap;\n}\n\n.restaurant-pages-nav .main-buttons-container {\n  display: grid;\n  grid-template-columns: repeat(4, auto);\n  justify-content: start;\n  align-items: start;\n  padding-bottom: 30px;\n  gap: 50px;\n}\n\n#logo {\n  padding: 0 10px;\n  position: absolute;\n  left: 10px;\n}\n\n#logo img {\n  min-width: 160px;\n}\n\nbutton:not(logo) {\n  align-items: center;\n  font-size: 1.5rem;\n}\n\nbutton {\n  background: none;\n  margin: 0;\n  padding: 0;\n  display: flex;\n  justify-content: center;\n  border: none;\n  color: white;\n  cursor: pointer;\n  transition: color 0.3s ease;\n\n  font-family: inherit;\n  font-weight: 100;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n}\n\n.main-buttons-container button.active {\n  color: var(--main-color);\n  font-weight: 300;\n  border-bottom: 1px solid var(--main-color);\n  padding-bottom: 1px;\n}\n\n.main-buttons-container button:hover {\n  border-bottom: 1px solid var(--main-color);\n  padding-bottom: 1px;\n}\n\n/* Main — Home Page */\n\n#content.home {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#content.home .background-image {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  z-index: -2;\n  filter: brightness(90%) saturate(50%);\n}\n\n#content.home::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.816) 5%, transparent);\n  z-index: -1;\n}\n\n.invitation-container {\n  display: grid;\n  justify-items: center;\n  align-items: start;\n  max-width: 600px;\n  padding-bottom: 150px;\n}\n\n.invitation-container p {\n  margin: 0;\n}\n\n.invitation-container .invitation {\n  font-family: var(--accent-font-family);\n  font-size: 2rem;\n  color: var(--main-color);\n}\n\n.invitation-container .header {\n  color: var(--main-color);\n  font-family: var(--support-font-family);\n  font-size: 4rem;\n  font-weight: 500;\n\n  margin: 0 0 30px;\n}\n\n.invitation-container .content {\n  margin-bottom: 30px;\n  text-align: center;\n}\n\nbutton.book {\n  border: 1px solid var(--main-color);\n  padding: 15px 20px;\n}\n\nbutton.book:hover {\n  text-decoration: none;\n  background-color: #c9ab811b;\n}\n\n/* Menu Page */\n\n.menu p {\n  margin: 0;\n}\n\n#content.menu {\n  display: grid;\n  justify-items: center;\n}\n\n.menu .background-image {\n  width: 100%;\n  height: 500px;\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: -2;\n  filter: brightness(60%) saturate(80%);\n  object-fit: cover;\n}\n\n#content.menu::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  background: linear-gradient(to bottom, rgb(0, 0, 0), transparent);\n  z-index: -1;\n}\n\n.menu > .header {\n  margin: 110px 0 180px;\n  font-size: 5rem;\n  text-align: center;\n  position: relative;\n  z-index: 1;\n  text-transform: uppercase;\n}\n\n.section-container {\n  display: grid;\n  justify-items: center;\n\n  gap: 60px;\n\n  max-width: 700px;\n}\n\n.section-container .header-container {\n  display: grid;\n  justify-items: center;\n}\n\n.section-container .section-item {\n  width: 700px;\n}\n\n.section-container .section-supheader {\n  margin: 0;\n  font-family: var(--accent-font-family);\n  font-size: 1.9rem;\n  text-transform: lowercase;\n\n  color: var(--main-color);\n}\n\n.section-items-container {\n  display: grid;\n  gap: 40px;\n}\n\n.section-container .section-subheader {\n  margin: 0;\n  font-size: 2rem;\n  text-transform: uppercase;\n  letter-spacing: 5px;\n}\n\n.section-container .section-row {\n  display: flex;\n  justify-content: space-between;\n  align-items: baseline;\n  gap: 6px;\n\n  margin-bottom: 6px;\n}\n\n.section-row .decorative-line {\n  content: \"\";\n  height: 7px;\n  border-top: 1px solid var(--main-color);\n  border-bottom: 1px solid var(--main-color);\n  flex: 1;\n}\n\n.section-container .section-name {\n  text-transform: uppercase;\n  color: var(--main-color);\n  font-weight: 400;\n  letter-spacing: 3px;\n}\n\n.section-container .section-price {\n  text-transform: uppercase;\n  color: var(--main-color);\n  font-weight: 400;\n}\n\n.section-container .section-description {\n  margin: 0;\n  font-size: 1rem;\n}\n\n.section-image {\n  width: 100%;\n  object-fit: cover;\n  height: 500px;\n  filter: brightness(80%) saturate(80%);\n  margin: 80px;\n}\n\n@media (min-width: 1000px) {\n  .section-image {\n    height: 800px;\n  }\n}\n\n/* Location Page */\n\n#content.location {\n  display: grid;\n  justify-items: center;\n}\n\n.location .background-image {\n  width: 100%;\n  height: 500px;\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: -2;\n  filter: brightness(60%) saturate(80%);\n  object-fit: cover;\n}\n\n#content.location::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 10%;\n  background: linear-gradient(to bottom, rgb(0, 0, 0), transparent);\n  z-index: -1;\n}\n\n.location > .header {\n  margin: 110px 0 180px;\n  font-size: 5rem;\n  justify-self: stretch;\n  text-align: center;\n  position: relative;\n  z-index: 1;\n  background: radial-gradient(\n    ellipse at center,\n    rgba(0, 0, 0, 0.567),\n    transparent\n  );\n  text-transform: uppercase;\n}\n\n.location .directions-container {\n  justify-self: center;\n  max-width: 800px;\n}\n\n.location .operation-container {\n  justify-self: center;\n  text-align: center;\n  margin-top: 40px;\n}\n\n.location h2.header {\n  justify-self: start;\n  font-size: 1.5rem;\n  margin: 0;\n}\n\n.location .map-container {\n  width: 100%;\n  display: grid;\n  justify-content: center;\n  margin-bottom: 20px;\n\n  filter: grayscale(100%);\n}\n\n.location .map-container p {\n  margin-bottom: 10px;\n  font-weight: 400;\n  font-size: 18px;\n}\n\n/* About Page */\n\n.about .background-image {\n  width: 100%;\n  height: 500px;\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: -2;\n  filter: brightness(50%) saturate(50%);\n  object-fit: cover;\n}\n\n#content.about::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 10%;\n  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.816), transparent);\n  z-index: -1;\n}\n\n#content {\n  display: grid;\n  justify-items: center;\n}\n\n.about > .header {\n  justify-self: stretch;\n  text-align: center;\n  margin: 150px 0 150px;\n  font-size: 5rem;\n  background: radial-gradient(\n    ellipse at center,\n    rgba(27, 26, 14, 0.364),\n    transparent\n  );\n  text-transform: uppercase;\n}\n\n.about-us-data {\n  font-size: 1.5rem;\n  padding: 0 50px;\n  max-width: 600px;\n  text-align: center;\n}\n\n.chef-info-container {\n  display: grid;\n  justify-items: center;\n\n  margin: 50px;\n}\n\n.chef-info-container p {\n  margin: 0;\n}\n\n.chef-info-container img {\n  height: auto;\n  width: 250px;\n  border-radius: 50%;\n}\n\n.chef-info-container .chef-quote {\n  font-family: var(--accent-font-family);\n  font-size: 2.8rem;\n  max-width: 900px;\n  margin: 10px;\n  color: var(--wide-font-color);\n}\n\n.chef-info-container .chef-name {\n  color: var(--main-color);\n  font-weight: 400;\n  font-size: 2.2rem;\n  margin-bottom: 5px;\n}\n\n.chef-info-container .chef-title {\n  font-weight: 300;\n  color: var(--wide-font-color);\n  margin-bottom: 80px;\n}\n\n.chef-info-container::after {\n  content: \"\";\n  display: block;\n  border-bottom: 1px solid var(--main-color);\n  width: 100%;\n  margin: 1rem 0;\n}\n\n.contacts-container {\n  display: grid;\n  justify-items: start;\n\n  gap: 10px;\n  margin-bottom: 100px;\n}\n\n.contacts-container .header {\n  justify-self: center;\n  font-size: 2.5rem;\n}\n\n.contacts-container p {\n  margin: 0;\n}\n\n/* Mobile Layout */\n\n#hamburger:hover {\n  transform: scaleX(-1);\n}\n\n.main-buttons-container.mobile.visible {\n  display: grid;\n  grid-template-columns: 1fr;\n  position: absolute;\n  top: 100%; \n  width: 100%;\n  left: 0;\n  background-color: rgb(1, 7, 23);\n  z-index: 2;\n  gap: 0;\n  margin: 0;\n  padding: 0;\n}\n\n.restaurant-pages-nav {\n  position: static;\n}\n\n.main-header {\n  position: relative;\n}\n\n.main-buttons-container.mobile.visible button {\n  border-bottom: none;\n  padding: 10px;\n}\n\n.main-buttons-container.mobile.visible button:hover {\n  background-color: rgb(4, 14, 40);\n  width: 100%;\n}\n\n@media screen and (max-width: 800px) {\n  .restaurant-pages-nav .main-buttons-container {\n    display: none;\n  }\n\n  .main-header {\n    height: 100px;\n    justify-content: right;\n    background-color: rgb(2, 6, 16);\n  }\n\n  .restaurant-pages-nav #logo {\n    top: 26px;\n  }\n\n}\n\n@media screen and (min-width: 800px) {\n  #hamburger {\n    display: none;\n  }\n}\n"],"sourceRoot":""}]);
